.app {
  text-align: center;
}

.app-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.app-header {
  background-color: #145be9;
  height: 2.5rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  font-size: 1.5rem;
  color: white;
}

.header-item {
  padding-right: 0.75rem
}

.config-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.config-row-item {
  padding-left: 1rem;
  min-width: 2rem;
}

.score {
  font-weight: bold;
  font-size: 2rem;
  padding-top: 0.5rem;
}

.question-container {
  width: 51000%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 0.5rem;
}

.question {
  font-size: 1rem;
  padding-left: 0.5rem;
  width: 100%;
}

.answer {
  padding-left: 0.5rem;
  padding-top: 0.5rem;
  width: 100%;
}

.answer input[type="number"] {
  width: 5rem;
  font-size: 1rem;
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

.answer input[type="number"]::-webkit-inner-spin-button,
.answer input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.answer input[type="submit"] {
  font-size: 1rem;
  margin-left: 0.5rem;
}

.result {
  font-size: 1.5rem;
  font-weight: bold;
  color: red;
  min-height: 2.5rem;
  display: block;
}

.result.correct {
  color: green;
}

.label-text {
  padding-right: 0.5rem;
}

.medium-integer {
  width: 5rem;
}

.symbol {
  font-size: 1.5rem;
}

.hidden {
  display: none !important;
}

.switch {
  position: relative;
  display: inline-block;
  width: 2.5rem;
  height: 1.5rem;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 1rem;
  width: 1rem;
  left: 0.25rem;
  bottom: 0.25rem;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(1rem);
  -ms-transform: translateX(1rem);
  transform: translateX(1rem);
}

/* Rounded sliders */
.slider.round {
  border-radius: 2rem;
}

.slider.round:before {
  border-radius: 50%;
}

.modal {
  display: block;
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 5% auto; /* 5% from the top and centered */
  padding: 20px;
  border: 1px;
  border-color: #888;
  border-style: outset;
  border-radius: 5px;
  width: 80%; /* Could be more or less, depending on screen size */
  color: black;
}

/* The Close Button */
.modal-close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.modal-close:hover,
.modal-close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
